@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
html {
  overflow: hidden;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-perspective: 1px;
          perspective: 1px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  overflow-x: hidden;
  overflow-y: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}
body {
  background: linear-gradient(-1deg, rgb(56, 120, 217), rgb(255, 255, 255));

  background-size: 100%;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: top left;
  display: flex;
  font-weight: 400;
  font-family: "Fira Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 300;
  font-family: "Fira Sans", sans-serif;
}

body,
html,
.App,
#root,
.auth-wrapper {
  width: 100%;
  height: 100%;
}

i {
  margin-left: 15px;
  color: rgb(17, 16, 16);
}

.navbar-light {
  /* #383e44 */
  background-color: transparent;
  position: fixed;
  top: 0;
}
.auth-wrapper {
  display: flex;
  width: 85%;
  height: auto;
  padding-top: 15vh;
  box-sizing: border-box;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  margin: auto;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.auth-wrapper::before {
  content: "";
  width: auto;
  height: 100vh;
  top: 0;
  left: 0;
  position: absolute;
  z-index: -2;
  -webkit-transform: translateZ(-1px) scale(2);
          transform: translateZ(-1px) scale(2);
}

.form-group {
  width: 350px;
}

.auth-inner {
  height: auto;
  width: auto;
  margin: auto;
  background: transparent;
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  opacity: 0.9;
}

.auth-wrapper .form-control:focus {
  border-color: black;
  box-shadow: none;
}
.frontPageSlideImage {
  width: 100%;
  height: 900px;
}
.cardsContainer {
  display: flex;
  flex-wrap: wrap;
}

.cardContainer {
  max-width: 325px;
  margin: auto;
  margin-top: 20px;
}

.cardImage {
  width: 325px;
  height: 225px;
}
.textArea {
  overflow-y: scroll;
  height: 135px;
}
.priceText {
  color: green;
  text-align: end;
  font-size: 18px
}
.dateBoxUser {
  padding-left: 50px;
}
.followers {
  text-align: end;
}

.cardImage {
  width: 325px;
  height: 225px;
}
.textArea {
  overflow-y: scroll;
  height: 135px;
}
.dateText {
  font-size: 14px;
}
.priceText {
  color: green;
  text-align: end;
}
.dateBoxAdmin {
  padding-left: 65px;
}

.modalInput {
  width: 350px;
}
.modal-content{
  width: 400px;
}
.addTripForm {
  display: flex;
  flex-direction: row;
}

.addFormRight {
  margin: 20px;
  display: flex;
  flex-direction: column;
}
.addFormLeft {
  margin: 20px;
  display: flex;
  flex-direction: column;
}
.addTripHeader {
  text-align: center;
}

